.gulpicon-test{
    @include pseudo-icon('.svg-icon-greencheck-60', after);
}

.grunticon-test{
    .icon-icon-greencheck-16{
        display: block;
    }

    .icon-icon-greencheck-60{
        display: none;
    }

    @media (min-width: $md_break){
        .icon-icon-greencheck-16{
            display: none;
        }

        .icon-icon-greencheck-60{
            display: block;
        }
    }
}

.clearfix {
  @include clearfix();
}

// Remove the focus from the main-element
#innhold {
    outline: 0 none;
}

// Internet Explorer message
// --------------------------------
#ie_msg {
    position: relative;
    width: 100%;
    @include rem('max-width', 650px);
    display: block;
    margin: 0 auto;
    text-align: center;
    
    div {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 10;
        background-color: $water;
        @include rem('border', 1px solid $spindle);
        border-top: 0 none;
        @include rem('padding', 5px 10px 7px);
        color: $black;
        font-size: 80%;
    }
    
    h4 {        
        margin: 0;
        font-size: 110%;
        font-weight: 400;
    }
    
    p {        
        margin: 0;
    }
}

// Breadcrumbs general style
// ---------------------------
.bc_container {
    position: relative;
    @include clearfix();
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
}
.bc_label {
    position: relative;
    z-index: 10;
    display: none;    
    float: left;
    @include rem('margin-top', 6px);
        
    &.mobile {
        @include rem('width', 102px);
    }        
    &.desktop {
        @include rem('width', 77px);
    }
}
.bc_list_container {
    position: relative;
    float: right;
    @include rem('height', 32px);
    overflow: hidden;
}
.bc_list {
    display: inline-block;
    @include rem('width', 5000px);
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    left: 0;
    top: 0;
    li {
        float: left;
        display: none;
        @include rem('margin-right', 16px);
        position: relative;
        z-index: 0;        
        a {
            display: block;
            background-color: $white_sand;
            @include rem('border', 2px solid $white_sand);
            @include rem('padding', 4px 3px 3px 7px);
            @include border-radius($base_border_radius $border_radius_large $border_radius_large $base_border_radius);
            
            &:focus {
                outline: 0 none;
                border-color: $link_outline_color;
                @include border-radius(0);
            }
        }
        &.bc_current {
            color: $emperor;
            @include rem('border', 1px solid $alto);
            @include rem('padding', 5px 7px 4px);
            margin: 0;
            @include border-radius();
        }
    }
}

// Breadcrumbs, small screens only
// ---------------------------------
@media (max-width: $md_break_max) {
    .bc_label.mobile {
        display: inline-block;
    }
    .bc_list_container {
        @include calc(width, "100% - 6.375rem");
        @include rem('margin-bottom', 18px);
        
        &:after {
            position: absolute;
            right: 0;
            top: 0;
            display: block;
            content: "";
            @include rem('width', 14px);
            @include rem('height', 32px);
            background: transparent;
            background: linear-gradient(to right, rgba($white, 0), $white);
        }
    }
    .bc_list {
        @include rem('width', 600px);
    }
    li.bc_last_link {
        display: inline-block;
        margin-right: 0;
        @include rem('margin-left', 14px);
        
        a {
            @include rem('padding-right', 8px);
            @include border-radius($border_radius_large $base_border_radius $base_border_radius $border_radius_large);
            
            &:before {
                position: absolute;
                top: 0;
                @include rem('left', -13px);
                z-index: -1;
                content: "";
                @include rem('border-top', 16px solid transparent);
                @include rem('border-right', 16px solid $white_sand);
                @include rem('border-bottom', 16px solid transparent);
                @include border-radius();
            }
        }
    }
}

// Breadcrumbs, bigger screens
// ---------------------------------
@media (min-width: $md_break) {
    .bc_label .mobile {
        display: none;
    }
    .bc_label {
        &.desktop {
            display: inline-block;
        }
        &.separator:before {
            position: absolute;
            z-index: 11;
            @include rem('right', -3px);
            @include rem('top', -10px);
            display: block;
            content: "";
            background-color: $zombie;
            @include rem('width', 1px);
            @include rem('height', 40px);
        }
        &.separator:after {
            position: absolute;
            z-index: 12;
            @include rem('right', -15px);
            @include rem('top', -10px);
            display: block;
            content: "";
            @include rem('width', 12px);
            @include rem('height', 40px);
            opacity: .6;
            background: transparent;
            background: linear-gradient(to right, $white, rgba($white, 0));
        }
    }
    .bc_list_container {        
        @include calc(width, "100% - 4.8125rem");
        @include rem('margin-bottom', 21px);
    }
    .bc_list {
        li {
            display: inline-block;
            a:after {
                position: absolute;
                top: 0;
                @include rem('right', -13px);
                z-index: -1;
                content: "";
                @include rem('border-top', 16px solid transparent);
                @include rem('border-left', 16px solid $white_sand);
                @include rem('border-bottom', 16px solid transparent);
                @include border-radius();
            }
        }  
        &.too_small {
            left: auto;
            @include rem('right', -14px);
        }
    }  
}
@media (min-width: $lg_break) {
    .bc_list_container {
        @include rem('margin-bottom', 32px);
    }
}



// Page header
// ---------------------------
.page_header {
    @include rem('border-bottom', 2px solid $zombie);
    @include rem('padding-bottom', 3px);
    @include rem('margin-bottom', 19px);
    
    h1 {
        color: $black;
        @include rem('font-size', 26px);
        line-height: ( 28px / 26px );
        @include rem('min-height', 35px);
        margin: 0;
        @include hyphens();
        
        .page_header_logo {
            float: left;
            @include rem('margin', -2px 8px 0 0);
            @include rem('max-height', 30px);
            @include rem('font-size', 8px);
        }
    
        @media only screen and (min-width: $md_break) {
            @include rem('font-size', 32px);
            line-height: ( 35px / 32px );
        
            .page_header_logo {
                @include rem('margin-top', 3px);
            }
        }
    }
    
    .page_header_parent {
        margin: 0;
        min-height: inherit;
        @include rem('font-size', 18px);
        line-height: ( 28px / 18px );
        @include SourceSansProRegular();
    }
    
    .print_link_container {
        display: none;
    }
}
    
// Flag / Norsk / English
.page_header.page_header_with_flag {
    @include flexbox();
    @include justify-content(space-between);

    h1 {
        @include order(0);
        @include flex(1 1 auto);
    }

    .flag_link_container {
        float: right;
        @include order(1);
        @include align-self(flex-end);
        @include flex(0 0 auto);
        
        a {
            display: none;
            @include rem('margin-left', 14px);
        }
        
        a.active {
            display: block;
        }
        
        @media (max-width: $md_break_max) {
            .lang {
                // screen-reader-text
                clip: rect(1px, 1px, 1px, 1px);
                height: 1px;
                width: 1px;
                overflow: hidden;
                position: absolute !important;
            }
        }
            
        @media (min-width: $md_break) {
            a {
                position: relative;
                @include rem('padding-right', 1px);
                @include rem('padding-left', 42px);

                // .english
                // .norsk
                .icon {
                    position: absolute;
                    @include rem('top', 6px);
                    @include rem('left', 2px);
                }
        
                .lang {
                    text-decoration: underline;
                    @include rem('font-size', 17px);
                    line-height: ( 21px / 17px );
                }
            }
            
        }
    }
}

@media (min-width: $md_break) {
    .page_header_with_print {
        @include flexbox();
        @include justify-content(space-between);

        h1 {
            @include order(0);
            @include flex(1 1 auto);
        }
        
        .print_link_container {
            display: inherit;
            float: right;
            @include order(1);
            @include align-self(flex-end);
            @include flex(0 0 auto);

            a {
                position: relative;
                display: block;
                text-decoration: underline;
                @include rem('padding', 5px 0 5px 34px);
                @include rem('margin-left', 20px);
            }

            .icon-print {
                position: absolute;
                left: 0;
                @include rem('top', 2px);
            }
            .print_link_txt {
            }
        }
    }
}


// H2 Page header - with a link
// ------------------------------
.h2_page_header_with_link {
    @include rem('border-bottom', 2px solid $zombie);
    @include rem('padding-bottom', 3px);
    @include rem('margin-bottom', 19px);

    h2 {
        color: $black;
        @include rem('font-size', 26px);
        line-height: ( 28px / 26px );
        @include SourceSansProRegular();
        @include rem('min-height', 28px);
        margin: 0;
        @include hyphens();
        
        a {
            color: $black;
        }
        
        .page_header_link {
            position: relative;
            color: $link_color;
            display: inline-block;
            @include rem('padding-left', 16px);
          
            .arrow-icon {
                @include single-arrow-icon($direction: 'left', $size: 'small', $display: 'inline-block');
                right: inherit;
                @include rem('left', -11px);
            }
        }
        
        .page_header_logo {
            float: left;
            @include rem('margin', -5px 20px 0 -17px);
            @include rem('max-height', 30px);
            @include rem('font-size', 8px);
        }
    
        @media only screen and (min-width: $md_break) {
            @include rem('font-size', 32px);
            line-height: ( 35px / 32px );
        
            .page_header_logo {
                @include rem('margin-top', 3px);
            }
        }
    }
}


// Module header
// ---------------------------
.module_header {
    color: $black;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include SourceSansProRegular();
