        margin: 0;
        color: $black;
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
    }
    
    h2 {
        @include SourceSansProRegular();
    }
    
    @media (min-width: $md_break) {
        @include flexbox();
        @include justify-content(space-between);

        h1 {
            @include order(0);
            @include flex(1 1 auto);
            @include rem('font-size', 30px);
            line-height: ( 35px / 30px );
        }
        
        .print_link_container {
            display: inherit;
            float: right;
            @include order(1);
            @include align-self(flex-end);
            @include flex(0 0 auto);

            a {
                position: relative;
                display: block;
                text-decoration: underline;
                @include rem('font-size', 17px);
                line-height: ( 21px / 17px );
                @include rem('padding', 4px 0 3px 34px);
                @include rem('margin-left', 20px);
            }

            .icon-print {
                position: absolute;
                left: 0;
                @include rem('top', -1px);
            }
            .print_link_txt {
            }
        }
    }
}

// h2 Module header med feillenke
// ---------------------------
.module_header_with_error_link{
    @include rem('border-bottom', 2px solid $zombie);
    @include flexbox();
    @include justify-content(space-between);
    align-items: baseline;
    @include rem('margin', 2px 0 22px);

    h2{
        @include rem('padding-bottom', 2px);
        @include rem('margin', 0);
        @include SourceSansProRegular();
    }
}


// Page footer
// ---------------------------
.page_footer {
    @include rem('border-top', 1px solid $alto);
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
    @include rem('padding-top', 14px);
    @include rem('margin-top', 34px);
    @include rem('margin-bottom', 42px);
    text-align: left;
    
    a {
        color: $link_color_hover; // Darker link color on small text links for AAA-accessibility
    }

    &.center {
        @media (max-width: $md_break_max) {
            text-align: center;
            .meta_info {
