// Tables
// -------------------------------------
.odd_even_table {
  @include rem('font-size', 20px);
  line-height: ( 28px / 20px );
  @include rem('margin', 0 0 12px);
  border-collapse: separate;
  @include border-radius($base_border_radius $base_border_radius 0 0);
  
  tr:nth-child(even) {
    background-color: $water;
  }
  
  caption {
    display: none;
  }
  
  thead {
    th {
      color: $emperor;
      @include rem('padding-top', 3px);
      @include rem('padding-bottom', 6px);
      background-color: $white_sand;
      
      &:first-child {
        @include border-radius($base_border_radius 0 0 0);
      }
      
      &:last-child {
        @include border-radius(0 $base_border_radius 0 0);
      }
    }
  }
  
  th,
  td {
    @include rem('border-width', 0 0 1px 0);
    @include rem('padding', 10px 12px);
    
    &:last-child {
      @include rem('border-right-width', 1px);
    }
  }
}


// Files
// -------------------------------------

a.file {
    display: block;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    text-decoration: none;
    line-height: ( 24px / 24px );
    background: url(../images/file.svg) no-repeat 0 4px transparent;
    @include rem('padding-left', 50px);
    @include rem('min-height', 24px);

    &.pdf {
        background-image: url(../images/pdf.svg);
    }
    /*
    &.doc,
    &.docx {
        background-image: url(../images/doc.svg);
    }
    &.ppt {
        background-image: url(../images/ppt.svg);
    }
    &.xls,
    &.xlsx {
        background-image: url(../images/xls.svg);
    }
    &.zip {
        background-image: url(../images/zip.svg);
    }
    */

    &:focus,
    &:hover,
    &:active {
        .file_title {
            text-decoration: underline;
        }
    }

    .meta {
        display: block;
        @include rem('font-size', 18px);
        line-height: ( 28px / 18px );
        text-transform: uppercase;
        color: $black;
    }
}

// RSS list & article
// -------------------------------------
.rssicon_floatRight,
.rssicon {
    a {
        position: relative;
        float: right;
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
        @include rem('margin', 9px 0 3px);
        @include rem('padding-left', 31px);
        text-decoration: underline;
        
        &:before {
            content: "";
            position: absolute;
            left: 0;
            @include rem('top', -4px);   
            background: url($main_sprite_url) no-repeat -256px -256px transparent; //.rss_icon
            @include rem('height', 32px);
            @include rem('width', 32px);
        }
    }
}

// Contact buttons
// -------------------------------------
.contact_button {
  display: block;
  position: relative;
  @include rem('font-size', 20px);
  line-height: ( 28px / 20px );
  @include rem('margin-bottom', 21px);
  @include clearfix();
  background-color: $white_sand;
  @include rem('border', 1px solid $spindle);
  @include rem('padding', 4px 8px 6px 36px);
  @include border-radius();
    
  .icon:first-child {
    position: absolute;
    @include rem('top', 2px);
    @include rem('left', 2px);
  }
  
  &:hover,
  &:active {    
  }
  
  .contact_button_info {
    float: right;
    display: inline-block;
  }
}


// Frontpage contact
// --------------------------------------------
.frontpage_contact {
    @include rem('padding', 0 21px 7px);
    
    .contact_content dd {
        @include rem('margin-bottom', 14px);
    }
}


