// -------------------------------------
.contact_content_employee_page_dl {
    @include rem('margin-left', 2px); // 2px indent because of medium_size-icons
    
    dd {
        @include rem('margin-bottom', 12px);
    }
}



// Map link with ikon
// -------------------------------------
.map_link {
  float: right;
  text-align: center;
  text-decoration: underline;
  @include rem('font-size', 16px);
  line-height: ( 21px / 16px );
  @include rem('margin', -6px 4px 6px 12px);
  
  .icon.map_icon {
    margin: 0 auto;
  }

  &:hover .icon.map_icon,
  &:active .icon.map_icon,
  &:focus .icon.map_icon {
    background-position: -224px -64px;
  }
}










// Module item
// -------------------------
.module_container {
    width: 100%;
    @include clearfix();
    
    .module_image_container {
        @include rem('margin-bottom', 14px);

        .module_image_link {
            display: block;
            width: 100%;
        }
        .module_image {
            display: block;
            width: 100%;
        }
    }

    .module_content_container {
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
        .module_content_title {
            @include rem('font-size', 22px);
            line-height: ( 28px / 22px );
            @include SourceSansProRegular();
        }
        .module_content_date {
            @include rem('font-size', 16px);
            line-height: ( 21px / 16px );
        }
        .module_content_excerpt p {
            @include rem('margin-bottom', 14px);
        }
    }

    @media (min-width: $md_break) {
        .module_image_container {
            float: left;
            width: 48%;
            @include rem('margin', 4px 28px 14px 0);
        }

        &.module_equal_heights {
            @include flexbox();

            .module_image_container,
            .module_content_container {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex();
            }
        }
    }
}


// Module item - box style
// -------------------------
.module_box_style {
    @include rem('padding', 21px 21px 7px);
    @include rem('border', 1px solid $zombie);
    background-color: $rum_swizzle;
    @include border-radius();
    @include box-sizing();
    // @include rem('margin-top', 14px);
    @include rem('margin-bottom', 6px);
    @include hyphens();
    
    .module_content_excerpt {
            @include rem('margin-bottom', 14px);
    }
}


// Module item - list style
// -------------------------
.module_list_container {
    padding-left: 0;
    
    li {
        list-style-image: none;
    }
    
    .module_container {
        @include rem('padding-bottom', 7px);
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('margin-bottom', 21px);
        @include clearfix();
    }
}

// Module item - Inside .link_list
// -------------------------
.link_list .module_container a.module_image_link {
    padding: 0;
}
 .link_list .module_container a.module_content_title {       
    padding: 0;
     
    &:hover,
    &:active {
        text-decoration: underline;
    }
}

// Module item - Byråd
.module_container .councilor {
    @include rem('margin', 7px 0 14px);
}


// Notice (default) + warning
// -------------------------
.notice_container,
.warning_container {
    position: relative;
    @include rem('padding', 63px 21px 7px);
    @include rem('border', 2px solid $spindle);
    background-color: $water;
    @include border-radius();
    @include box-sizing($boxmodel: content-box);
    
    &:before {
        content: "";
        position: absolute;
        @include rem('top', 21px);
        @include rem('left', 16px);      
        background: url($main_sprite_url) no-repeat -160px -192px transparent; //.notice_icon
        @include rem('height', 32px);
        @include rem('width', 32px);
    }
    
    .notice_inner,
    .warning_inner {
        @include clearfix();
        @include rem('max-width', 600px);
        @include rem('margin-bottom', 7px);
            
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
    
        a {
            text-decoration: underline;
        }

        &__actions {
            display: flex;
            align-items: baseline;
            justify-content:flex-end;
            flex-wrap: wrap-reverse;

            & a {
                @include rem('margin-left', 24px);
            }
            & button {
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                @include rem('margin-left', 24px);
            }
        }
    }
    
    @media (min-width: $md_break) {
        @include rem('padding-top', 21px);
        @include rem('padding-left', 116px);
        @include rem('min-height', 88px);
        margin-top: 0;
        
        &:before {
            @include rem('top', 25px);
            @include rem('left', 26px);
            background-position: 0 -192px; // .notice_icon.large_size
            @include rem('height', 64px);
            @include rem('width', 64px);
        }
    
        .notice_inner,
        .warning_inner {
            
            p, ul {
                @include rem('margin-bottom', 14px);
            }
        }
    }
}


// Warning special
// -------------------------
.warning_container {
    background-color: $white;
    border-color: $main_red;

    &:before {
        @include rem('left', 19px);        
        background-position: -160px -224px; // .warning_icon
    }
    
    @media (min-width: $md_break) {
        @include rem('padding-left', 130px);
        
        &:before {
            @include rem('left', 17px);
            background-position: -64px -192px; // .warning_icon.large_size
            @include rem('width', 96px);
        }
    }
    
}


// Small notice
// -------------------------
.small_notice_container {
    position: relative;
    @include rem('padding', 21px);
    @include rem('border', 1px solid $spindle);
    background-color: $water;
    @include border-radius();
    @include rem('margin-top', 14px);
    
    &:before {
        content: "";
        position: absolute;
        @include rem('top', -16px);
        @include rem('left', 5px);
        background: url($main_sprite_url) no-repeat -160px -192px  transparent; //.notice_icon
        @include rem('height', 32px);
        @include rem('width', 32px);
    }
    
    a {
        text-decoration: underline;
    }
}


// Accordion
// ---------------------------
.accordion_container {
    @include rem('margin-bottom', 7px);
    @include clearfix();
    clear: both;
    
    @media (max-width: $lg_break_max) {
        @include rem('margin-bottom', 42px);
    }
}
.accordion_item {
    @include rem('margin', 0 0 7px);
}

.open_all_accordions {
    text-align: center;
    @include rem('border-bottom', 1px solid $alto);
    @include rem('margin', 21px 0);

    a {
        display: inline-block;
        @include rem('padding', 7px 12px);
    }
}

.accordion_header {
    margin: 0;
    @include SourceSansProRegular();
    @include rem('font-size', 28px);
    line-height: (35px / 28px);

    a {
        position: relative;
        display: block;
        color: $black;
        @include rem('padding', 10px 10px 10px 18px);
        @include rem('min-height', 24px);
        @include rem('border-bottom', 1px solid $zombie);
        @include transition($property: background-color, $duration: 100ms);
      
        &:focus {
            border-bottom-color: transparent;
        }
        
        &:hover,
        &:active {
            color: $black;
            background-color: $rum_swizzle;
            text-decoration: none;
        }

        .arrow-icon {
            @include single-arrow-icon($direction: 'down', $size: 'large', $display: 'inline-block', $color: $danube);
            @include rem('top', -7px);
            @include rem('right', 9px);
        }
    }
}

.accordion_item.active .arrow-icon {
    @include single-arrow-icon($direction: 'up', $size: 'large', $display: 'inline-block', $color: $danube);
    @include rem('top', -7px);
    @include rem('right', 9px);
}

.accordion_content {
    display: none;
    background-color: $bianca;
    @include rem('padding', 21px 15px 7px);
    @include rem('min-height', 42px);
    border-top: 0 none;
  
    .accordion_inner_content_header {
        @include rem('font-size', 22px);
        line-height: ( 28px / 22px );
        @include rem('border-bottom', 1px solid $janna);
        @include rem('margin-bottom', 7px);
    }

    p + ul {
        @include rem('margin-top', -21px);
    }
  
    .accordion_inner_content_header + ul {
        margin-left: 0;
    }
    
    @media (min-width: $md_break) {
        @include rem('padding', 21px 18px);
    }
}

// Toggled accordion
.active .accordion_content {
    display: inherit;
}


// Tiltak
// ---------------------------
.tiltak {
    
    .ingress {
        @include rem('margin-bottom', 42px);
    }
    
    .kriterium {
        @include rem('margin-bottom', 42px);
        
        .kriteriumoverskrift {            
            color: $black;
            @include rem('font-size', 21px);
            line-height: ( 28px / 21px );
            @include SourceSansProSemibold();
            @include rem('margin', 34px 0 8px);
        }
        
        .kriteriumsliste {
            @include rem('margin-bottom', 28px);
            
            ul {
                @include rem('margin-top', 14px);
            }
            
            a,
            ul li a {
                text-decoration: underline;
            }
        }
        
        .lesMerTekst a {
            text-decoration: underline;
        }
    }
    
    .headerKriterier {
        text-transform: uppercase;
        @include rem('margin-bottom', 42px);
    }
    
    .footer {
        text-transform: uppercase;
        @include rem('margin-bottom', 42px);
    }
}


// Var dette hjelpsomt / Fornøyd med denne siden
// ---------------------------

.feedback {
    text-align: center;
    @include rem('padding', 21px 21px 7px);
    background-color: $water;
    @include rem('border', 1px solid $spindle);
    @include border-radius();
    @include rem('margin-bottom', 35px);
    @include rem('margin-top', 35px);

    @media (min-width: $md_break) {
        margin-top: 0;
    }
}

.feedback_header {
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('margin-bottom', 14px);
}

.feedback_is_submitted_message {
    display: none;
    @include rem('font-size', 22px);
    line-height: ( 28px / 22px );
    @include rem('margin', 14px 0);
}


.feedback_form {
    @include rem('margin', 0 0 14px);
  
    .feedback_yes_no {
        display: inline-block;
    }
  
    // Ja / Nei radio-buttons
    .feedback_btn {
        float: left;
        cursor: pointer;
        display: inline-block;
        margin: 0;
        @include rem('padding', 2px 16px 5px);
        @include rem('min-width', 60px);

        &:focus {
            border-color: transparent;
            position: relative;
            z-index: 10;
        }
    }
  
    .feedback_btn.yes {
        @include border-radius($base_border_radius 0 0 $base_border_radius);
        &.active {
            border-right-color: $form_main_color;
        }
    }
  
    .feedback_btn.no {
        border-left: 0 none;
        @include border-radius(0 $base_border_radius $base_border_radius 0);
        &.active {
            @include rem('margin-left', -1px);
            @include rem('border-left', 1px solid $form_main_color);
        }
    }
  
    textarea {
        resize: none;
        border-color: $alto;
        @include border-radius();
        border-bottom-right-radius: 0;
        margin-bottom: 0;
        @include box-shadow(none);
        @include rem('padding', 7px 16px 7px);
        @include rem('min-height', 160px);
        @include rem('margin-top', 14px);

        &:focus {
            border-color: $form_main_color;
        }

        &::-webkit-input-placeholder {
            color: $emperor;
            @include SourceSansProRegularItalic();
        }

        &:-moz-placeholder {
            color: $emperor;
            @include SourceSansProRegularItalic();
        }

        &::-moz-placeholder {
            color: $emperor;
            @include SourceSansProRegularItalic();
        }

        &:-ms-input-placeholder {
            color: $emperor;
            @include SourceSansProRegularItalic();
        }
    }
  
    .feedback_collapsed {
        display: none;
    }
    
    .feedback_info {
        float: left;
        @include calc(width, "100% - 7.5rem");
        text-align: left;
        @include rem('margin', 7px 0 0);      
    }
  
    .feedback_submit {
        position: relative;
        color: $white;
        background-color: $form_main_color;
        border: 0 none;
        margin-bottom: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        @include rem('padding', 4px 42px 6px 14px);
        @include rem('margin-bottom', 7px);

        &:after {
            position: absolute;
            content: "";
            @include rem('top', -5px);
            @include rem('right', 10px);
            @include rem('width', 14px);
            @include rem('height', 32px);
            background: url(../images/list_arrows_white.svg) no-repeat left top transparent;
        }

        &[disabled] {
            border: 1px solid $alto !important;
            border-top: 0 none !important;
            &:after {
                background-image: url(../images/list_arrows_grey.svg);
            }
        }
    }
  
    @media (min-width: $md_break) {
        .feedback_yes_no {
        }
    }
}

// After submit
.is_submitted {
    .feedback_header,
    .feedback_form {
        display: none;
    }
    .feedback_is_submitted_message {
        display: inherit;
    }
}



// Header
// -----------------------------------------------------------------------------

#global_header {
    @include rem('border-bottom', 2px solid $main_red);
    @include rem('margin-bottom', 18px);
    @include clearfix();
  
    .header_container {
        position: relative;
    }
  
    @media (min-width: $md_break) {
        @include rem('margin-bottom', 28px);
    }
    
    @media (min-width: $lg_break) {
        @include rem('margin-bottom', 33px);
    }
}


// Logo
// ---------------------------
.logo {
    display: block;
    @include rem('padding', 10px);
    @include rem('width', 184px);
    @include rem('height', 84px);
    @include rem('margin', 4px auto 0);
    @include box-sizing();

    span {
        display: inline-block;
        background: url($main_sprite_url) no-repeat -14px 0 transparent;
        text-indent: -3000em;
        overflow: hidden;
        @include rem('width', 164px);
        @include rem('height', 64px);
    }

    @media (min-width: $md_break) {
        display: inline-block;
        @include rem('padding', 4px);
        @include rem('width', 172px);
        @include rem('height', 72px);
        @include rem('margin', 25px 0 14px -6px);
    }

    @media (min-width: $lg_break) {
        @include rem('margin-bottom', 13px);
    }
}

.share_and_print_logo {
    display: none;
}


// Navigation toggle
// ---------------------------
.nav_toggle_btn {
  position: absolute;
  @include rem('left', 10px);
  @include rem('top', 22px);
  color: $black;
  background-color: $rum_swizzle;
  @include rem('border', 1px solid $zombie);
  text-transform: uppercase;
  @include rem('font-size', 10px);
  line-height: ( 14px / 10px );
  text-align: center;
  @include rem('padding', 3px 5px 5px);
  @include border-radius();
  @include transition(background-color);
    
  &:hover {
    background-color: darken($rum_swizzle, 10);
    text-decoration: none;
  }

  .nav_toggle_icon {
    @include rem('height', 16px);
  }
  
  @media (min-width: $md_break) {
    display: none;
  }
}


// Login / Logout container
// ---------------------------

.login_container {
	position: absolute;
	@include rem('top', 14px);
	@include rem('right', 3px);

	.seperator {
		display: none;
	}
}


// Login link // MinSide
// ---------------------------
.login_link {
    display: inline-block;
    text-decoration: none;
    @include rem('padding', 4px);
    
	.login_icon {
		@include rem('width', 46px);
		@include rem('height', 46px);
	}
    
    .text {
        display: none;
    }
}



// Logout link
// ---------------------------
.mobile_logout_link {
    display: none;
    position: relative;
	@include rem('margin', 0 40px 30px 0);
    @include rem('padding', 10px 0 10px 45px);
    @include rem('font-size', 21px);
    line-height: ( 28px / 21px );
    @include rem('border-top', 1px solid $thunderbird);
    @include rem('border-bottom', 1px solid $thunderbird);
    
    .icon {
        position: absolute;
        @include rem('top', 8px);
        @include rem('left', 12px);
    }
    
    @media (min-width: $md_break) {
        display: none !important;
    }
}


.logout_link {
	display: none; // changes with .is_logged_in
}


// Is logged in
// ---------------------------
.is_logged_in {
    
    .mobile_logout_link {
        display: block;
    }
	
	.login_link .login_icon {
        background-position: -73px -265px; // .is_logged_in.login_icon
	}
    
    @media (min-width: $md_break) {
        .seperator,
        .logout_link {
            display: inline-block;
        }
    }
}



// Tablet
// ---------------------------
@media (min-width: $md_break) {
    .login_container {
        @include rem('top', 41px);
        @include rem('right', -6px);
        
        .login_link {
            position: relative;
            float: left;
            @include rem('padding', 8px 5px 8px 52px);

            .login_icon {
                position: absolute;
                @include rem('top', -1px);
                @include rem('left', -3px);
            }

            .text {
                display: inherit;
                @include SourceSansProRegular();
            }

            &:hover .text {
                text-decoration: underline;
            }
        }

        .seperator {
            position: relative;
            @include rem('top', 5px);
            @include rem('width', 1px);
            @include rem('height', 32px);
            background-color: $silver;
            @include rem('margin', 0 4px);
        }
        
        .logout_link {
            position: relative;
            float: right;
            @include rem('padding', 5px 0 4px);
        }
    }
}

@media (min-width: $md_break) and (max-width: $md_header_login_break_max) {
    .login_container .logout_link .text {
        // screen-reader-text
        clip: rect(1px, 1px, 1px, 1px); 
        height: 1px; 
        width: 1px; 
        overflow: hidden; 
        position: absolute !important;
    }
}


// Big tablet
// ---------------------------
@media (min-width: $md_header_login_break) {
    .login_container {

        .seperator {
            @include rem('top', 10px);
            @include rem('height', 22px);
        }
        
        .logout_link {
            @include rem('padding', 8px 7px 10px);

            .logout_icon {
                display: none;
            }
            
            .text {
                display: inline;
                @include SourceSansProRegular();
            }
        }
    }
}










// Main search
// ---------------------------
.main_search_container {
    @include rem('padding', 7px 10px 21px);

    form {
        position: relative;
    }

    .search_input {
        width: 100%;
        @include rem('border', 1px solid $form_main_color);
        margin: 0;
        @include rem('font-size', 21px);
        line-height: ( 28px / 21px );
        @include rem('padding', 6px 58px 6px 10px);
    }

    button {
        position: absolute;
        @include rem('top', 1px);
        @include rem('right', 1px);
        @include rem('bottom', 1px);
        color: $black;
        padding: 0;
        margin: 0;
        border-radius: none;
        border: 0 none;
        @include rem('border-left', 1px solid $form_main_color);
        @include border-radius(0 $base_border_radius .125rem 0);
        background-color: $danube;
        @include rem('width', 48px);
        @include rem-box-shadow(0 1px 0 rgba($white, 32/100) inset, 0 -1px 0 rgba($link_color, 32/100) inset);
        @include transition(background-color);

        .searchglass_icon {
            position: absolute;
            overflow: hidden;
            @include rem('top', 4px);
            @include rem('left', 8px);
        }

        &:hover,
        &:active {
            background-color: $danube_hover;
        }
    }

    @media (min-width: $md_break) {
        position: absolute;
        left: 0;
        right: 0;
        @include rem('top', 39px);
        margin: 0 auto;
        padding: 0;
        width: 50%;
    }
    
    @media (min-width: $md_break) and (max-width: $lg_break_max) {
        @include rem('padding-left', 40px);
    }
    
    .is_logged_in & {
        @media (min-width: $md_break) and (max-width: $md_header_login_break_max) {
            width: 46%;
            padding-left: 0;
        }
        @media (min-width: $md_header_login_break) {
            padding-left: 0;
            @include rem('padding-right', 40px);
        }
    }
}



// Navigation
// ---------------------------
@media (max-width: $md_break_max) {
  
  .main_nav {
    .nav_container {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: 100;
      width: 88%;
      color: $white;
      background-color: $nav_mobile_bg;
      @include rem-box-shadow(2px 0 4px rgba($black, 40/100));
      @include transition($property: margin-left, $duration: 200ms, $timing-function: ease);

      a {
        color: $white;
      }
    }
    &.main_nav_collapsed .nav_container {
      margin-left: -110%;
    }

    // Navigation - closing link - mobile
    .hide_nav_toggle_area {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: 99;
      width: 100%;
      opacity: 1;
      overflow: hidden;
      background-color: rgba($black, 48/100);
      @include transition($property: opacity, $duration: 240ms, $timing-function: ease, $delay: 50ms);
    }
    &.main_nav_collapsed .hide_nav_toggle_area {
      width: 0;
      opacity: 0;
    }
    
    .close_nav_btn {
      text-indent: -3000em;
      position: absolute;
      overflow: hidden;
      z-index: 1;
      @include rem('right', -38px);
      @include rem('top', 52px);
      background-color: $nav_mobile_bg;
      @include border-radius(0 $base_border_radius $base_border_radius 0);      
      @include rem('border', 3px solid $nav_mobile_bg);
      @include rem-box-shadow(3px 0 4px rgba($black, 24/100));
      @include box-sizing($boxmodel: content-box);
    }
  }
  
  // Main navigation list // mobile
  .main_nav_list {
    padding: 0;
    @include rem('margin', 42px 40px 25px 0);
    
    li {
      list-style-image: none;
      @include rem('border-bottom', 1px solid $thunderbird);
    }
    a {
      display: block;
      @include rem('padding', 10px 0 10px 20px);
      @include rem('font-size', 21px);
      line-height: ( 28px / 21px );
    }
    li.active a {
      @include rem('padding-left', 36px);
      background: url("../images/white_list_style_image.svg") no-repeat 21px 21px transparent;
    }
  }
    
  // Secondary navigation list // mobile
  .secondary_nav {
    padding: 0;
    @include rem('margin', 0 40px 30px 0);
    
    a {
      position: relative;
      display: inline-block;
      @include rem('padding', 7px 25px);
      @include rem('margin-left', 20px);
      
      .icon {
        position: absolute;
        @include rem('top', 6px);
        @include rem('left', -8px);
      }
    }
  }
}

@media (min-width: 650px) and (max-width: 700px) {
    
    // Prevent sliding when changing size from desktop to mobile
    .main_nav.main_nav_collapsed .nav_container {
        -webkit-transition: none !important;
        -moz-transition: none !important;
        -ms-transition: none !important;
        -o-transition: none !important;
        transition: none !important;
    }
}

@media (min-width: $md_break) {

  .main_nav {
  
    // Don't show this on desktop
    .hide_nav_toggle_area,
    .close_nav_btn {
      display: none;
    }
  
    // Main navigation list // tablet
    .main_nav_list {
      background-color: $white_sand;
      padding: 0;
      text-align: center;
      @include rem('margin', 8px 0 0);
      @include rem('border', 1px solid $alto);
      border-bottom: 0 none;
      @include rem-box-shadow(0 1px 1px $white inset);
      @include border-radius($base_border_radius $base_border_radius 0 0);
      
      // Flexbox
      @include flexbox();
      @include flex-wrap($value: nowrap);
      // @include justify-content($value: center); // flex-start / flex-end / space-between / space-around / center
      
      li {
        display: inline-block;
        list-style-image: none;
        text-align: center;
        @include flex($fg: 1, $fs: 0, $fb: auto);
        // $fg = flex-grow    = give each item 1 share of extra width
        // $fs = flex-shrink  = don’t let items shrink at all
        // $fb = flex-basis   = start items at 200 pixels wide
      }
      a {
        display: block;
        color: $black;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('padding', 6px 14px 4px);
      }
      li.active {
        @include rem('margin-top', -3px);
          
        a {
            position: relative;
            color: $white;
            @include rem('padding-top', 9px);
            text-shadow: 0 1px 2px #a31d22;
            @include border-radius($base_border_radius $base_border_radius 0 0);
            background: $main_red;
            background: linear-gradient(#f53137, $main_red);
          }
      }
    }

    // Secondary navigation list // desktop
    .secondary_nav {
      display: none;
    }
  }
}

// iPad font-fix for the active element
@media only screen 
    and (min-device-width: 768px) 
    and (max-device-width: 1024px) 
    and (-webkit-min-device-pixel-ratio: 1) {
        nav .main_nav_list li.active a {
            @include SourceSansProSemibold();
        }
}


@media (min-width: $lg_break) {

  .main_nav {
  
    // Main navigation list // desktop
    .main_nav_list {
      background-color: transparent;
      border: 0 none;
      box-shadow: inherit;
      @include border-radius(0);
      
      li {
        @include flex($fg: 1, $fs: 0, $fb: 19%);
        @include rem('margin', 0 3px 0 2px);
        background-color: $white_sand;
        @include rem('border', 1px solid $alto);
        border-bottom: 0 none;
        @include rem-box-shadow(0 1px 1px $white inset);
        @include border-radius($base_border_radius $base_border_radius 0 0);
        
        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-right: 0;
        }
      }
      a {
        @include rem('font-size', 21px);
        line-height: ( 28px / 21px );
        @include rem('padding-bottom', 6px);
      }
      li.active {
        border: 0 none;
          
        a {
            @include rem('padding-top', 10px);
            @include rem('padding-bottom', 7px);
        }
      }
    }
  }
}



// Media queries for min medium break
// ********************************************

@media (min-width: $md_break) {

  // Layout
  // ---------------------------
  .desktop_absoluted {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }

  .desktop_centered {
    margin: 0 auto;
    max-width: $site_max_width;
    width: 92%;
    @include clearfix();
  }
  
  .md_relative_container {
    position: relative;
  }

}






// Footer
// -----------------------------------------------------------------------------

footer {
    position: relative;
    @include clearfix();
    text-align: center;
    @include rem('margin-top', 72px);
    
    .footer_mountain_container {
        position: relative;
        z-index: -1;
        background-color: $white;
        padding-top: 100% * 26 / 302;

        .mountains {
            position: absolute;
            @include rem('bottom', -1px);
            left: 0;
            right: 0;
            height: 100%;
            background: url(../images/7-fjell.svg) no-repeat center bottom transparent;
            background-size: contain;
        }
    }
  
    .footer_link_container {
        position: relative;
        z-index: 1;
        @include rem('border-top', 2px solid $main_red);
        background-color: $white_sand;
        @include rem('min-height', 64px);

        @media (min-width: $md_break) {
        @include rem('min-height', 100px);
        }
        @media (min-width: $lg_break) {
        @include rem('min-height', 100px);
        }

        .footer_links {
            position: relative;
            margin: 0 auto;
            width: 92%;
            max-width: $site_max_width;

            @media (min-width: $md_break) {
                width: 83%;
            }
            @media (min-width: $lg_break) {
                width: 92%;

                // Flexbox
                @include flexbox();
                @include flex-wrap($value: nowrap);

                a {
                    @include flex($fg: 1, $fs: 0, $fb: auto);
                    // $fg = flex-grow    = give each item 1 share of extra width
                    // $fs = flex-shrink  = don’t let items shrink at all
                    // $fb = flex-basis   = start items at 200 pixels wide
                }
            }
        }
    }
  
    .chat_link_area {
        position: relative;
        width: 92%;
        @include rem('margin', 0 auto);
        max-width: $site_max_width;

        .chat_link {
            display: none;
            position: absolute;
            bottom: 0;
            right: 0;
            margin: 0;
            @include rem('font-size', 20px);
            line-height: ( 28px / 20px );
            color: $white;
            background-color: $main_red;
            @include rem('padding', 3px 52px 3px);
            @include border-radius($base_border_radius $base_border_radius 0 0);

            .chat_icon {
                position: absolute;
                @include rem('top', 3px);
                @include rem('left', 8px);
            }
            
            .online_icon,
            .offline_icon {
                position: absolute;
                @include rem('top', 2px);
                @include rem('right', 5px);
            }


            &:focus{
                z-index: 999;
            }
        }
    }
  
    // Footer icons // mobile
    @media (max-width: $md_break_max) {
        // Not visible
        .contact_link,
        .press_link,
        .intern_link,
        .some_footer_link {
            display: none;
        }
        // Big links visible
        .sitemap_link,
        .english_link {
            display: block;
            float: left;
            width: 50%;
            @include rem('padding', 14px 0);
            .link_outer {
                display: block;
                @include rem('padding', 6px 12px 7px);
            }
            .link_inner {
                display: inline-block;
                position: relative;
                @include rem('padding-left', 24px);
                @include box-sizing();
            }
            .icon {
                position: absolute;
                @include rem('top', -3px);
                @include rem('left', -7px);
            }
        }
        // Special link style
        .sitemap_link .link_outer {
            @include rem('border-right', 1px solid $alto);
        }
    }
    
    @media (min-width: $md_break) and (max-width: $lg_break_max) {
        // Big links on top
        .contact_link,
        .sitemap_link,
        .intern_link {
            @include rem('padding', 18px 0);
            @include rem('margin', 15px 0 13px);
            float: left;
            display: block;
            width: 33.333333%;
            .link_outer {
                display: block;
                @include rem('padding', 6px 12px 7px);
            }
            .link_inner {
                display: inline-block;
                position: relative;
                @include rem('padding-left', 24px);
                @include box-sizing();
            }
            .icon {
                position: absolute;
                @include rem('top', -3px);
                @include rem('left', -7px);
            }
        }
        // Special link style
        .contact_link {
        text-align: left;
            .link_outer {
                padding-left: 0;
            }
        }
        .sitemap_link {
            .link_outer {
                @include rem('border-right', 1px solid $alto);
                @include rem('border-left', 1px solid $alto);
            }
        }
        .intern_link {
            text-align: right;
            .link_outer {
                padding-right: 0;
            }
        }
        // Small links bellow
        .english_link,
        .press_link,
        .some_footer_link {
            float: left;
            display: block;
            @include rem('padding', 14px 0);
            .link_outer {
                display: block;
                @include rem('padding', 6px 12px 7px);
            }
            .link_inner {
                display: inline-block;
                position: relative;
                @include rem('padding-left', 24px);
                @include box-sizing();
            }
            .icon {
                position: absolute;
                @include rem('top', -3px);
                @include rem('left', -7px);
            }
        }
        .english_link {
        @include rem('margin', 0 22px 0 -12px);
        }
        .some_footer_link {
            position: absolute;
            @include rem('top', 100px);
        }
        .facebook_link {
            @include rem('right', 208px);
        }
        .twitter_link {
            @include rem('right', 109px);
        }
        .instagram_link {
            @include rem('right', -12px);
        }
        .english_link {
            position: absolute;
            @include rem('top', 100px);
            left: 0;
        }
        .press_link {
            position: absolute;
            @include rem('top', 100px);
            @include rem('left', 110px);
        }
    }
    @media (min-width: $lg_break) {        
        // Big links // desktop
        .contact_link,
        .sitemap_link,
        .english_link,
        .press_link,
        .intern_link {
            @include rem('padding', 6px 0);
            @include rem('margin', 27px 0 25px);
            float: left;
            display: block;
            .link_outer {
                display: block;
                @include rem('padding', 6px 12px 7px);
            }
            .link_inner {
                display: inline-block;
                position: relative;
                @include rem('padding-left', 24px);
                @include box-sizing();
            }
            .icon {
                position: absolute;
                @include rem('top', -3px);
                @include rem('left', -7px);
            }
        }
        // Special link style
        .contact_link {
            text-align: left;
            .link_outer {
                padding-left: 0;
            }
        }
        .contact_link .link_outer,
        .sitemap_link .link_outer,
        .english_link .link_outer,
        .press_link .link_outer,
        .intern_link .link_outer {
            @include rem('border-right', 1px solid $alto);
        }
        
        
        .some_footer_link {
            display: block;
            text-align: center;
            @include rem('padding', 6px 0);
            @include rem('margin', 29px 3px 23px);
            float: left;
            @include rem('max-width', 40px);
            
            .link_outer,
            .link_inner {
                display: inline-block;
            }
            
            .some_footer_link_text {
                // screen-reader-text
                clip: rect(1px, 1px, 1px, 1px);
                height: 1px;
                width: 1px;
                overflow: hidden;
                position: absolute !important;
            }
            
            &.facebook_link {
                @include rem('margin-left', 16px);
            }
        }
    }
    /*
    @media (min-width: $xl_break) {
        // Big links // big desktop
        .contact_link,
        .sitemap_link,
        .english_link,
        .press_link,
        .intern_link,
        .facebook_link,
        .twitter_link,
        .instagram_link {
            .link_outer {
                @include rem('padding', 6px 36px 7px);
            }
        }
    }
    */
}


